import { useStore, useRoute, onMounted, useContext, ref, useFetch } from '@nuxtjs/composition-api'
import functions from '@/compositions/functions'
import { useWpApi } from '@/compositions/wp'
const perPage = 2

export default {
    name: 'ArchiveNews',
    setup () {
        const route = useRoute()
        const store = useStore()
        const queryPage = ref('')
        const fields = ref('')
        const posts = ref('')
        const category = ref('')
        const { page: firstQueryPage = 1 } = route.value.query // fetch 需要重置
        const { loadImage } = functions()
        queryPage.value = firstQueryPage

        const { $wp, i18n } = useContext()
        const { getArchiveFields, getPosts, getCategoryNews } = useWpApi({ wp: $wp, lang: i18n.locale })
        const { fetch } = useFetch(async () => { // 需要在 fetch 取 query
            const { page = 1, 'cat-news': catNews } = route.value.query
            fields.value = await getArchiveFields('news')
            posts.value = await getPosts('news', { page, perPage, catNews })
            category.value = await getCategoryNews()
            queryPage.value = +page // refresh pagination
        })

        fetch()

        onMounted(() => {
            store.dispatch('ADD_LOADING_STACK', loadImage())
        })

        return { fields, posts, category, page: queryPage }
    },
    head () {
        const i18nSeo = this.$nuxtI18nHead({ addSeoAttributes: true })
        const seo = this.fields.seo || {}
        return {
            title: seo?.title,
            htmlAttrs: {
                ...i18nSeo.htmlAttrs,
            },
            meta: [
                { hid: 'og:title', property: 'og:title', content: seo?.title },
                { hid: 'og:site_name', property: 'og:site_name', content: seo?.title },
                { hid: 'name', itemprop: 'name', content: seo?.title },
                { hid: 'twitter:title', name: 'twitter:title', content: seo?.title },
                { hid: 'description', name: 'description', content: seo?.desc },
                { hid: 'og:description', property: 'og:description', content: seo?.desc },
                { hid: 'twitter:description', name: 'twitter:description', content: seo?.desc },
                { hid: 'og:url', property: 'og:url', content: `${process.env.APP_URL}/${this.getRouteBaseName(this.$route)}` },
                { hid: 'og:image', property: 'og:image', content: seo?.thumb },
                { hid: 'twitter:image', name: 'twitter:image', content: seo?.thumb },
                { hid: 'image', itemprop: 'image', content: seo?.thumb },
                ...i18nSeo.meta,
            ],
            link: [
                ...i18nSeo.link,
            ],
        }
    },
    computed: {
    },
    watch: {
        '$route.query': '$fetch',
    },
}
